.header__modal{
    width: 100%;
    height: 20px;
    display: flex;
    padding: 0px;
    margin-top: 5px;
    align-items: center;
    justify-content: space-between;
}

.family__title{
    padding: 0px;
    font-size: 14px;
    color: white;
    font-weight: 600;
    margin: 0px !important;
}

.label__family{
    display: flex;
    border: none;
    margin-left: 5px;
    align-items: center;
    border-radius: 4px ;
    justify-content: center;
    background-color: white;
    box-shadow: .5px .5px 3px rgba(83, 83, 83, .8);
}

.text__name__family{
    padding: 4px;
    font-size: 10px;
    color: black;
    margin: 0px !important;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
