.box__record__activity{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
    padding: 30px;
}

.data__current__record {
    width: 220px;
    height: 40px;
    display: flex;
    border-radius: 5px;
    align-items: center;
    margin-bottom: 20px;
    justify-content: center;
    background-color: #00b88a;
}

.data__current__record span{
    color: white;
    font-family: Arial, Helvetica, sans-serif;
}

.content__records__app{
    position: relative !important;
    height: 690px !important;
    overflow-x: auto;
}

.row__content__item__record{
    margin-bottom: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 60px;
}

.icon__item__record{
    width: 43px;
    height: 39px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    background-color: #009587;
}

.icon__item__record span{
    color: white;
}

.item__record{
    width: 100%;
    background-color: white;
    height: 40px;
    box-shadow: 0 1px 1px rgba(0,0,0,.1);
    border-radius: 3px;
    color: #444;
    margin: 0 15px 20px 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 5px 0 20px;
}

.item__record span{
    font-family: Arial, Helvetica, sans-serif;
}

.item__record .hour__record{
    color: #999;
    padding: 0 10px 0 0;
    font-size: 12px;
}

@media(max-width: 1130px) {
    .item__record{
        padding: 10px;
        height: auto;
    }
    .row__content__item__record{
        margin-bottom: 5px;
        height: auto;
    }
}

@media(max-width: 710px){
    .icon__item__record{
        display: none;
    }
    .item__record span{
        line-height: 20px;
    }
}

@media(max-width: 385px){
    .item__record{
        font-size: 15px;
    }
}
