/* ABOUT DASHBOARD Style */
/* @author Hector Rodrigues Da Silva */

.content__about {
    width: 90%;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    flex-wrap: wrap;
}

.box__about {
    background-color: #25be98e5;
    box-shadow: 0 8px 10px 0 rgba(31, 70, 88, 0.096);
    width: 100%;
    height: 100%;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    flex-wrap: nowrap;
    padding-top: 30px;
}

.box__about::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #F5F5F5;
}

.box__about::-webkit-scrollbar {
    width: 10px;
    background-color: #F5F5F5;
}

.box__about::-webkit-scrollbar-thumb {
    background-color: #087c5fe5;
    background-image: -webkit-linear-gradient(45deg, rgba(255, 255, 255, .2) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, .2) 50%, rgba(255, 255, 255, .2) 75%, transparent 75%, transparent)
}

.box__about .item__about {
    width: auto;
    padding: 15px 40px;
    margin: 0;
    height: auto;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    flex-wrap: wrap;
}

.item__about h1 {
    font-size: 25px;
    font-family: font-medium;
    text-align: start;
    color: white;
}

.item__about p {
    font-size: 17px;
    font-family: font-regular;
    color: white;
    line-height: 23px;
    text-align: justify;
    padding: 10px 0;
}

@media(max-width: 1230px){
    .content__about{
        width: 100%;
    }
}