/* APP Style */
/* @author Hector Rodrigues Da Silva */
.body__home {
    height: 100%;
    display: flex;
    margin-top: 65px;
    background-color: #ecf0f5;
}

.content__wrapper__home {
    width: 100%;
    height: 100%;
    margin: 0 0 0 240px;
    box-sizing: border-box;
    transition: 0.4s !important;
}

.form-control:focus, .css-1pahdxg-control{
    border-color: #00b88a !important;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset, 0 0 4px #00b88a !important;
}

.row {
    width: 100% !important;
}

.swal-title {
    font-weight: normal !important;
    font-family: Arial, Helvetica, sans-serif !important;
}

.__react_component_tooltip {
    font-family: Arial, Helvetica, sans-serif;
}

@keyframes spinner {
    to {
        transform: rotate(360deg);
    }
}
/* This is the class name given by the Font Awesome component when icon contains 'spinner' */
.fa-spinner {
    /* Apply 'spinner' keyframes looping once every second (1s)  */
    animation: spinner 1s linear infinite;
}

@media (max-width: 1220px) {
    .content__wrapper__home {
        transition: 0.4s !important;
        margin: 0 0 0 90px !important;
    }
    .main__sidebar {
        width: 90px !important;
        transition: 0.4s !important;
    }
    .health__unit__name h1 {
        display: none !important;
    }
    .info__profile__user {
        display: none !important;
    }
    .span__li__item__menu {
        display: none !important;
    }
    .header__aside {
        display: none !important;
    }
    .item__aside{
        justify-content: center !important;
    }
}

@media (max-width: 800px) {
    .content__wrapper__home {
        margin: 0 !important;
    }
    .body__home {
        height: auto !important;
        margin-top: 67px !important;
    }
    .main__sidebar {
        display: none !important;
    }
    .button__sidebar__mobile {
        display: block !important;
    }
}

@media (min-width: 800px) {
    .sidebar__mobile {
        display: none !important;
    }
}

@media (max-width: 611px) {
    .body__home {
        height: auto !important;
        margin-top: 111px !important;
    }
}
