/* CONTENT DASHBOARD Style */
/* @author Hector Rodrigues Da Silva */

@font-face {
    font-family: font-light;
    src: url("../../../fonts/subset-Graphik-Light.woff2");
}

@font-face {
    font-family: font-medium;
    src: url("../../../fonts/subset-Graphik-Medium.woff2");
}

@font-face {
    font-family: font-regular;
    src: url("../../../fonts/subset-Graphik-Regular.woff2");
}

.main__menu {
    width: 10%;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    margin: 0 30px 0 0;
}

.title__menu {
    width: 100%;
    display: flex;
    padding-top: 20px;
    padding-bottom: 20px;
    justify-content: flex-start;
    flex-direction: column;
}

.title__menu h1 {
    color: #172b3e;
    font-size: 19px;
    font-weight: bold;
    font-family: font-medium, serif;
}

.button__item {
    outline: none;
    padding: 12px;
    cursor: pointer;
    color: #4d5c6d;
    font-size: 14px;
    font-weight: bold;
    transition: 0.4s;
    border-radius: 7px;
    border: 2px solid #8498ae;
    background-color: rgb(255, 255, 255);
}

.button__item:hover {
    color: white;
    transition: 0.4s;
    background-color: #4d5c6d;
}

.mobile__csv {
    display: none;
}

.content__analytic {
    width: 90%;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
}

.boxes__total__info {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 20px 0;
}

@media (max-width: 1700px) {
    .box__information {
        width: 550px !important;
    }
}

@media (max-width: 1614px) {
    .title__menu h1 {
        font-size: 14px;
    }
}

@media (max-width: 1575px) {
    .box__information {
        width: 490px !important;
    }
}

@media (max-width: 1442px) {
    .box__information {
        width: 430px !important;
    }
}

@media (max-width: 1245px) {
    .box__information {
        width: 100% !important;
    }
    .content__wrapper__dashboard {
        margin: 110px 20px 20px 20px;
    }
}

@media (max-width: 1230px) {
    .content__wrapper__dashboard {
        flex-direction: column;
    }
    .main__menu {
        width: auto;
        margin: 0;
        flex-direction: row;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .content__analytic {
        width: 100%;
    }
    .main__menu .item__menu {
        margin: 0 20px;
        width: auto;
    }
    .main__menu .item__data {
        width: auto;
    }
}

@media (max-width: 1018px) {
    .boxes__total__info {
        margin: 20px 12px !important;
    }
    .box__info__general {
        width: 100% !important;
        margin: 5px 0 !important;
    }
    .box__info__default {
        width: 100% !important;
        margin: 5px 0 !important;
    }
}

@media (max-width: 910px) {
    .main__menu .item__menu {
        margin: 0px 10px;
    }

    .main__menu .icon__menu-active {
        width: 10px;
        padding: 14px 17px;
        border-radius: 12px;
    }
    .icon__menu-active h1 {
        font-size: 13px !important;
    }

    .main__menu .icon__menu-disabled {
        width: 10px;
        padding: 14px 17px;;
        border-radius: 12px;
    }
    .icon__menu-disabled h1 {
        font-size: 13px !important;
    }

    .main__menu .item__data {
        display: none;
    }
    .item__menu p {
        font-size: 12px !important;
    }

    .csv__export .desk__csv {
        padding: 9px;
        font-size: 12px;
        border-radius: 5px;
    }
}

@media (max-width: 800px) {
    .desk__csv {
        display: none;
    }
    .mobile__csv {
        display: block;
        padding: 7px 10px !important;
        font-size: 14px !important;
        border-radius: 5px !important;
    }
    .main__menu .item__menu {
        margin: 0 15px;
    }
    .main__menu .csv__export {
        margin: 0;
    }
    .title__menu {
        display: none;
    }
    .item__menu {
        margin-top: 20px;
    }
    .content__wrapper__dashboard {
        margin: 160px 10px 0 10px;
        padding: 30px 10px;
    }
}

@media (max-width: 365px) {
    .mobile__csv {
        padding: 10px 4px !important;
        font-size: 11px !important;
        border-radius: 6px !important;
    }
}
