/* ASIDE Style */
/* @author Hector Rodrigues Da Silva */

@font-face {
    font-family: font-light;
    src: url("../../fonts/subset-Graphik-Light.woff2");
}

@font-face {
    font-family: font-regular;
    src: url("../../fonts/subset-Graphik-Regular.woff2");
}

.main__sidebar {
    position: fixed;
    width: 240px;
    height: 100%;
    background-color: rgba(37, 46, 51, 0.993);
    border-top-right-radius: 10px;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    transition: 0.4s;
    z-index: 20;
}

.health__unit__name {
    height: 55px;
    display: flex;
    align-items: center;
    background-color: #00b88a;
    justify-content: space-around;
}

.health__unit__name h1 {
    width: 190px;
    color: white;
    font-size: 14px;
    margin-left: 20px;
    font-weight: bold;
    text-align: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-transform: uppercase;
    font-family: "Arial", sans-serif;
}

.health__unit__name .button__edit__unit__admin:hover{
    opacity: 0.7;
}

.profile__user {
    height: 70px;
    display: flex;
    justify-content: center;
}

.profile__user__content {
    width: 100%;
    height: 100%;
    padding: 8px;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
}

.profile__user__content img {
    cursor: pointer;
    border-radius: 50px;
}

.profile__user__content img:hover {
    opacity: 0.9;
}

.info__profile__user {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.profile__user .name__profile__user {
    width: 145px;
    color: white;
    font-size: 13px;
    font-weight: bold;
    line-height: 25px;
    text-align: center;
    letter-spacing: 1px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-transform: capitalize;
    font-family: font-light, serif;
}

.profile__user .office__profile__user {
    width: 100px;
    color: white;
    font-size: 13px;
    text-align: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-transform: capitalize;
    font-family: font-regular, serif;
}

.all__items__aside {
    display: flex;
    flex-direction: column;
}

.header__aside {
    padding: 15px;
    display: flex;
    justify-content: flex-start;
    background-color: rgba(26, 34, 38, 0.855);
}

.header__aside li {
    color: #7c96a0;
    font-size: 15px;
    text-transform: uppercase;
    font-family: font-light, serif;
}

.item__aside {
    padding: 15px;
    display: flex;
    color: #7c96a0;
    cursor: pointer;
    transition: 0.3s;
    font-size: 14px;
    justify-content: flex-start;
}

.item__aside li {
    font-weight: bold;
    font-family: font-regular, serif;
}

.item__aside:hover {
    transition: 0.3s;
    color: #cbdde4;
    background-color: rgba(20, 26, 29, 0.855);
    -webkit-box-shadow: 12px 6px 5px -3px rgba(21, 27, 34, 0.199);
    -moz-box-shadow: 12px 6px 5px -3px rgba(21, 27, 34, 0.199);
    box-shadow: 12px 6px 5px -3px rgba(21, 27, 34, 0.199);
}


/* ASIDE MOBILE */
.button__sidebar__mobile{
    display: none;
    right: 0;
    width: 35px;
    height: 35px;
    position: fixed;
    margin: 10px;
    color: #ffffff;
    border-radius: 4px;
    border: 1px solid rgb(117, 117, 117);
    background-color: black;
    cursor: pointer;
    transition: 0.3s;
    z-index: 300;
}

.button__sidebar__mobile:hover{
    transition: 0.3s;
    color: #ffffff;
    background-color: rgb(11 156 120);
}

.sidebar__mobile{
   display: none;
   width: 100%;
   position: fixed;
   background-color: rgb(38 47 52);
   z-index: 20;
}

.sidebar__mobile ul{
   width: 100%;
   margin: 0;
   display: flex;
   justify-content: center;
   flex-direction: column;
}

.sidebar__mobile li{
    height: 40px;
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: 5px 20px;
}

.sidebar__mobile li:hover{
    background-color: rgb(64 80 88);
}

.sidebar__mobile li span{
    width: 100%;
    text-align: justify;
    color: #cacaca;
    font-family: font-regular;
}
