/* NAVBAR Style */
/* @author Hector Rodrigues Da Silva */
@font-face {
    font-family: font-medium;
    src: url("../../fonts/subset-Graphik-Medium.woff2");
}

/* NAVBAR DESKTOP */
header {
    width: 100%;
    z-index: 10;
    height: 65px;
    position: fixed;
    top: 0 !important;
    left: 0 !important;
    right: 0 !important;
    background-color: white;
    border: 1px solid rgba(28,28,28,.07);
    box-shadow: 0 7px 6px 0 rgba(33,43,54,.04);
}

header .nav__desk {
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
}

header .nav__desk .nav__logo {
    width: auto;
    height: 100%;
}

header .nav__desk .nav__logo:hover {
    opacity: 0.8;
    cursor: pointer;
}

header .nav__desk ul {
    margin: 0;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    flex-direction: row;
    justify-content: flex-end;
}

header .nav__desk ul li {
    padding: 0 30px;
    font-size: 16px;
    color: #7b8ea1;
    cursor: pointer;
    font-family: font-medium, serif;
}

header .nav__desk ul li:hover {
    color: #aab5c0;
}

@media(max-width: 800px){
    header{
        height: auto;
    }
    header .nav__desk{
        justify-content: center !important;
    }
    header .nav__desk .nav__logo{
        width: 300px;
        height: auto;
    }
    header .nav__desk ul li{
        padding: 10px 15px;
    }
}
