.container__calls {
    height: 600px;
    overflow-y: auto;
    padding: 20px;
}

.container__calls h5 {
    font-size: 20px;
    margin-bottom: 0.5rem;
    font-weight: 500;
    line-height: 1.2;
    padding: 15px 0;
}

.container__calls p {
    font-size: 18px;
    padding-top: 10px;
    text-align: justify;
    border-bottom: 1px solid rgb(224, 224, 224);
    padding-bottom: 20px;
}

.button__calls {
    border: 0;
    outline: none;
    color: white;
    font-size: 15px;
    padding: 5px 8px;
    border-radius: 5px;
    background-color: #28a745;
}

.button__calls:hover {
    opacity: 0.9;
}
